.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.gradient-black-70{
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.25) 0% ,rgba(255, 255, 255, 1) 100%);
  background-clip: text;
  --webkit-background-clip: text;
  -moz-background-clip: text;
  color: transparent;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


@keyframes opacity-100-to-0{
  0%{
    opacity: 1;
  }
  50%{
    opacity: 0;
  }
  100%{
    opacity: 1;
  }
}

.opacity-100-to-0{
  animation: opacity-100-to-0 2s infinite;
}

@keyframes move-text-to-top{
  0%{
    transform: translateY(0px);
  }
  45%{
    transform: translateY(0px);
  }
  50%{
    transform: translateY(-55px);
  }
  95%{
    transform: translateY(-55px);
  }
  100%{
    transform: translateY(0px);
  }
}

@keyframes move-text-to-top-md{
  0%{
    transform: translateY(0px);
  }
  45%{
    transform: translateY(0px);
  }
  50%{
    transform: translateY(-75px);
  }
  95%{
    transform: translateY(-75px);
  }
  100%{
    transform: translateY(0px);
  }
}

.move-text-to-top{
  animation: move-text-to-top 7s infinite;
}


@media screen and (min-width: 740px){
  .move-text-to-top{
    animation: move-text-to-top-md 7s infinite;
  }
}